import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Cards from '../Cards';


class ShowPost extends Component {
    constructor(props) {
        super(props)
        this.state = {
            row: {}
        }
    }
    componentDidMount() {
        fetch("https://opso.ml/board/" + this.props.match.params.id)
            .then(res => {
                return res.json();
            })
            .then(json => {
                this.setState({
                    row: JSON.parse(json)
                });
            })
            .catch(err => console.log(err));
    }
    render() {
        return (
            <div>
                <Grid className="center-box" container spacing={40} justify="center" alignItems="center">
                    <Grid className="paper" item>
                        <Paper>
                            <Cards key={this.state.row.id} author={this.state.row.author} contents={this.state.row.contents} id={this.state.row.id} match={this.props.match} />
                        </Paper>
                    </Grid>
                    <Button variant="contained" href="/post">글쓰는 곳으로 돌아가기</Button>
                </Grid>
            </div >
        );
    }
}

export default ShowPost;